* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --black: #111;
  --black_1: #202024;
  --grey: #b4afaf;
  --lighygrey: #e0e0e0;
  --violet: #4f2ee8;
  --white: #fff;
  --font-size-extrabig: 48px;
  --font-size-big: 32px;
  --font-size-normal: 22px;
  --font-size-normal_1: 18px;
  --font-size-small: 16px;
  --fint-size-small_1: 14px;
  --font-size-small_2: 12px;
}

img {
  border-radius: 8px;
}

h3 {
  letter-spacing: -4%;
  text-align: left;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

body {
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.home {
  background-color: #fff;
  border: 1px solid #ddd;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.left-section {
  flex-direction: column;
  width: 356px;
  height: 100%;
  display: flex;
}

.categories-title {
  color: #4f2ee8;
  letter-spacing: 2%;
  font-size: 18px;
  font-weight: 700;
}

::-webkit-scrollbar {
  width: 8px;
  height: 167px;
}

::-webkit-scrollbar-track {
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: e9e9e0;
}

.categories-block {
  width: 356px;
  height: 472px;
  overflow-y: scroll;
}

.categories-block li {
  color: #202024;
  cursor: pointer;
  padding: 18px 0;
  font-size: 18px;
  line-height: 32px;
  list-style: none;
}

.categories-block li:hover {
  color: var(--violet);
  cursor: pointer;
  padding: 18px 0;
  font-size: 18px;
  line-height: 32px;
  list-style: none;
}

.charities-slider-block {
  width: 356px;
  padding: 142px 20px 0;
}

.main-title {
  color: #000;
  letter-spacing: -4%;
  text-align: left;
  padding: 0 0 40px;
  font-size: 48px;
  font-weight: 700;
  line-height: 52px;
}

.books {
  color: #4f2ee8;
  letter-spacing: -4%;
  font-size: 48px;
  font-weight: 700;
  line-height: 52px;
}

.books-list-block {
  flex-wrap: wrap;
  justify-content: space-between;
  width: calc(100% - 356px);
  max-width: 1196px;
  padding: 10px;
  display: flex;
}

.books-list-block ul {
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.books-list-block li {
  text-align: center;
  flex: 0 0 calc(20% - 20px);
  max-width: 180px;
  margin-bottom: 20px;
  margin-right: 20px;
}

.book-card img {
  object-fit: cover;
  width: 180px;
}

.modal-card-container {
  z-index: 999;
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.book-card {
  text-align: center;
  background: #fff;
  border-radius: 5px;
  width: 90%;
  max-width: 300px;
  margin: 20px;
  padding: 20px;
  position: relative;
  top: 30%;
  overflow: hidden;
  box-shadow: 0 2px 4px #0003;
}

.close-button {
  cursor: pointer;
  color: #555;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.close-button:hover {
  color: #000;
}

.add-to-cart-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 20px;
}

.add-to-cart-button:hover {
  background-color: #0056b3;
}

.modalDiv {
  position: fixed;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

